import React from "react";
import "./logo.css";
const OrangeTheme = require("../../assets/headphones1.svg") as string;
const PurpleTheme = require("../../assets/headphones2.svg") as string;

export interface LogoProps {
  /**
   * Is this the principal call to action on the page?
   */
  primary?: boolean;
  /**
   * What background color to use
   */
  backgroundColor?: string;
  /**
   * How large should the button be?
   */
  size?: "small" | "medium" | "large";
  /**
   * What color should the logo be?
   */
  image?: "OrangeTheme" | "PurpleTheme";
  /**
   * Button contents
   */
  /**
   * Optional click handler
   */
  onClick?: () => void;
}

/**
 * Primary UI component for user interaction
 */
export const Logo: React.FC<LogoProps> = ({
  primary = false,
  size = "medium",
  image = "OrangeTheme",
  backgroundColor,
  ...props
}) => {
  const mode = image === "OrangeTheme"
    ? OrangeTheme
    : PurpleTheme;
  return (
    // <button
    //   type="button"
    //   className={['storybook-button', `storybook-button--${size}`, mode].join(' ')}
    //   style={{ backgroundColor }}
    //   {...props}
    // >
    //   {label}
    // </button>
    <img
      className="headphones-logo"
      src={mode}
      alt="I'm Listeninglogo"
    />
  );
};
